/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  font-family: "Open Sans", sans-serif;
  background-image: url(../img/bg-section-top-desktop-1.svg);
}

p {
  margin: 0;
  font-size: 1.6rem;
}

.container {
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

.seccion {
  padding: 2rem 0;
}

h1 {
  font-size: 2rem;
  font-family: "Poppins", sans-serif;
  line-height: 1.5;
}

h2 {
  font-size: 3rem;
  font-family: "Open Sans", sans-serif;
}

h3 {
  font-size: 1.8rem;
}

a {
  text-decoration: none;
}

[class$=__heading] {
  margin: 2rem 0;
}

.header {
  padding: 3rem 0;
}
.header__grid {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__logo {
  width: 12rem;
}
.header__btn {
  padding: 0.7rem 2.5rem;
  color: hsl(322deg, 100%, 66%);
  border-radius: 3rem;
  font-size: 1.6rem;
  position: relative;
  border: 1px solid hsl(322deg, 100%, 66%);
  transition: background-color 0.3s ease-in-out;
}
.header__btn:hover {
  border: 1px solid hsl(321deg, 100%, 78%);
  font-weight: 700;
}
.header__link {
  color: hsl(322deg, 100%, 66%);
  font-size: 1.3rem;
}

.community {
  padding: 0 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 3rem;
  margin-top: 7rem;
}
.community__title {
  color: hsl(192deg, 100%, 9%);
  text-align: center;
}
@media (min-width: 768px) {
  .community__title {
    font-size: 3rem;
  }
}
.community__paragraph {
  color: hsl(192deg, 100%, 9%);
  text-align: center;
  margin-bottom: 2rem;
  font-size: 1.3rem;
  line-height: 1.5;
}
@media (min-width: 768px) {
  .community__paragraph {
    max-width: 38rem;
  }
}
.community__btn {
  padding: 2rem 6rem;
  color: hsl(207deg, 100%, 98%);
  border-radius: 3rem;
  font-size: 1.6rem;
  position: relative;
  background-color: hsl(322deg, 100%, 66%);
  font-weight: 600;
  box-shadow: 0px 6px 20px rgba(0, 0, 0, 0.4);
  transition: background-color 0.3s ease-in-out;
}
.community__btn:hover {
  background-color: hsl(321deg, 100%, 78%);
}
.community__img-screen {
  margin: 5rem 0;
}
.community__grid {
  display: grid;
  gap: 7rem;
}
@media (min-width: 768px) {
  .community__grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 30rem;
  }
}
.community__img {
  width: 3rem;
}
.community__num {
  font-size: 5rem;
  margin: 0;
}
@media (min-width: 768px) {
  .community__num {
    font-size: 7rem;
  }
}
.community__num-paragraph {
  font-size: 1.2rem;
  text-align: center;
  margin: 1rem 0;
  color: hsl(210deg, 4%, 67%);
}

.grow {
  margin: 6rem 0;
}
.grow__grid {
  display: grid;
  gap: 7rem;
}
.grow__space {
  padding: 2rem 3rem;
}
.grow__bg {
  background-color: hsl(207deg, 100%, 98%);
}
@media (min-width: 768px) {
  .grow__bg {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
    gap: 10rem;
    padding: 4rem 5rem;
  }
}
@media (min-width: 768px) {
  .grow__card-img {
    grid-column: 2/3;
    padding: 0 2rem;
  }
}
@media (min-width: 768px) {
  .grow__card-text {
    grid-row: 1/2;
  }
}
@media (min-width: 1024px) {
  .grow__card-text {
    padding: 10rem;
  }
}
.grow__title {
  text-align: center;
  padding: 3rem 0 2rem 0;
  margin: 0;
}
@media (min-width: 768px) {
  .grow__title {
    font-size: 3rem;
    text-align: left;
  }
}
.grow__paragraph {
  text-align: center;
  line-height: 1.5;
  font-size: 1.3rem;
}
@media (min-width: 768px) {
  .grow__paragraph {
    text-align: left;
  }
}
.grow__bg-two {
  padding: 4rem 2.5rem;
}
@media (min-width: 768px) {
  .grow__bg-two {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
    gap: 10rem;
    padding: 4rem 5rem;
  }
}
@media (min-width: 768px) {
  .grow__card-img-two {
    grid-column: 1/2;
  }
}
@media (min-width: 768px) {
  .grow__card-tex-two {
    grid-column: 2/3;
  }
}

.build {
  text-align: center;
  margin: 20rem 0;
}
.build__title {
  margin-bottom: 6rem;
}
@media (min-width: 768px) {
  .build__title {
    font-size: 4rem;
  }
}
.build__btn {
  padding: 2rem 6rem;
  color: hsl(207deg, 100%, 98%);
  border-radius: 3rem;
  font-size: 1.6rem;
  position: relative;
  background-color: hsl(322deg, 100%, 66%);
  font-weight: 600;
  box-shadow: 0px 6px 20px rgba(0, 0, 0, 0.4);
  transition: background-color 0.3s ease-in-out;
}
.build__btn:hover {
  background-color: hsl(321deg, 100%, 78%);
}

.footer {
  color: hsl(207deg, 100%, 98%);
}
.footer__bg {
  background-color: #00252e;
  padding: 3rem 0;
}
.footer__grid {
  display: grid;
  gap: 4rem;
  padding: 0 2rem;
}
@media (min-width: 768px) {
  .footer__grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 30rem;
  }
}
.footer__title {
  text-transform: uppercase;
  padding: 1rem 0;
  font-weight: 700;
}
.footer__text {
  font-size: 1.2rem;
  line-height: 1.7;
}
@media (min-width: 768px) {
  .footer__section-one {
    grid-column: 2/3;
  }
}
@media (min-width: 768px) {
  .footer__section-two {
    grid-row: 1/2;
  }
}
@media (min-width: 1024px) {
  .footer__form {
    display: flex;
    align-items: baseline;
    gap: 2rem;
  }
}
.footer__form__input {
  width: 100%;
  padding: 0.6rem 0;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .footer__form__input {
    padding: 0.6rem 3rem;
    margin-right: 2rem;
  }
}
.footer__field {
  text-align: end;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .footer__field {
    max-width: 3rem;
  }
}
.footer__submit {
  background-color: #ff52bf;
  border-radius: 0.5rem;
  color: #f5faff;
  font-size: 1.4rem;
  font-weight: 600;
  padding: 1.3rem 4rem;
  position: relative;
  border: none;
  cursor: pointer;
}
.footer__submit:hover {
  background-color: hsl(321deg, 100%, 78%);
}
.footer__item {
  display: flex;
  gap: 1rem;
  margin: 2rem 0;
}
.footer__social {
  display: flex;
  gap: 2rem;
}
.footer__icon {
  color: hsl(207deg, 100%, 98%);
  font-size: 2rem;
}
.footer__icon:hover {
  color: #1ca7f1;
}
.footer__form__input-error {
  border: 2px solid red;
}
.footer__form__input-correct {
  border: 2px solid green;
}
.footer__error {
  color: red;
  margin: 1rem 0;
}